<template>
  <div class="popup">
    <!--  -->
    <Overlay
      ref="overlayRef"
      v-if="overlay"
      :show="show"
      @click="clickMask"
    ></Overlay>
    <!--  -->
    <transition :name="slideTransitionName">
      <div class="popup-content" :class="'to-' + position" v-show="show">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  name: 'popup',
  model: {
    prop: 'show',
    event: 'update:show',
  },
  props: {
    show: Boolean,
    position: {
      type: String,
      default: 'bottom',
    },
    overlay: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    slideTransitionName() {
      return 'slide_' + this.position;
    },
  },
  methods: {
    clickMask() {
      //  model  event  show.sync  v-model  show.sync
      this.$emit('update:show', false);
    },
  },
};
</script>
<style lang="scss" scoped>
// 
.slide_bottom-enter,
.slide_bottom-leave-active {
  transform: translate3d(0, 100%, 0);
}

// 
.slide_top-enter,
.slide_top-leave-active {
  transform: translate3d(0, -100%, 0);
}

// 
.slide_left-enter,
.slide_left-leave-active {
  transform: translate3d(-100%, 0, 0);
}

// 
.slide_right-enter,
.slide_right-leave-active {
  transform: translate3d(100%, 0, 0);
}

.popup {
  .popup-content {
    z-index: 2000;
    width: 100%;
    position: fixed;
    height: auto;
    background: #fff;
    transition: transform 0.3s;
    border-radius: 15px 15px 0 0;

    &.to-top {
      top: 0;
      left: 0;
    }
    &.to-bottom {
      bottom: 0;
      left: 0;
    }
    &.to-left {
      width: 50%;
      top: 0;
      left: 0;
    }
    &.to-right {
      width: 50%;
      top: 0;
      right: 0;
    }
  }
}
</style>
